.qpeIndexMainContainer {
    padding-top: 70px;
    flex: 1 1;
    display: flex;


    .qpeScribe-container {
        flex: 1 1;
        max-height: 612px;
        display: flex;
        flex-direction: column;
        padding: 24px;
        gap: 16px;

        .qpeTop-container {
            max-height: 20dvh;
            gap: 16px;
            display: flex;
            justify-content: center;
            flex-direction: column;

            .qpePatientName {
                font-weight: 600;
                font-size: 16px;
            }

            .qpeDetailsContainer {
                display: flex;
                gap: 10px;
                flex-direction: column;
                width: 100%;
                border-radius: 12px;
                border: 1px solid var(--border-color);

                .qpeSubDetailsContainer {
                    display: flex;
                    padding: 16px;
                    gap: 16px;
                    align-items: center;
                    width: 100%;

                    .qpeDetailsLeftContainer {
                        display: flex;
                        align-items: center;

                        .qpeProfilePic {
                            width: 40px;
                            height: 40px;
                        }
                    }

                    .qpeDetailsRightContainer {
                        display: flex;
                        flex-direction: column;

                        .qpePatientDataContainer {
                            display: flex;
                            gap: 10px;

                            .qpePatientData {
                                align-items: center;

                                display: flex;
                                gap: 6px;
                            }
                        }
                    }
                }
            }

            .qpeTabContainer {
                display: flex;
                gap: 10px;
                border-bottom: 1px solid var(--border-color);

                .qpeTabOption {
                    border: 1px solid transparent;
                    padding: 5px 10px;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    transition: 0.3s ease all;
                    flex-grow: 1;

                    &:hover {
                        background: rgba(0, 0, 0, 0.1);
                    }

                    &.qpeSelectedTab {
                        font-weight: 600;
                        color: var(--primary-color);
                        border: none;
                        border-bottom: 2px solid var(--primary-color);
                    }
                }
            }
        }

        .qpeOuterContainer {
            flex: 1 1;
            display: flex;
            flex-direction: column;
            overflow-y: auto;

            .qpeContextContainer {
                height: auto;
                border-radius: 10px;
                overflow-y: auto;
                flex-direction: column;
                display: flex;
                flex: 1 1;
                justify-content: space-between;
                position: relative;

                .qpeNoData {
                    flex: 1 1;
                    align-items: center;
                    display: flex;
                    justify-content: center;
                    border: 1px solid var(--border-color);
                }

                .qpeInnerContainer {
                    overflow-y: auto;
                    flex: 1 1;
                    display: flex;
                    flex-direction: column;


                    .qpeSoapHeader {
                        display: flex;
                        justify-content: space-between;
                        padding-right: 10px;

                        .qpeEditorButton {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background: none;
                            cursor: pointer;
                            border: none;
                        }
                    }

                    ul {
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        .qpeListItemContainer {
                            display: flex;
                            gap: 10px;
                            flex-direction: column;
                            padding: 0 30px;
                            margin: 10px 0 10px 0;

                            &.editMode {
                                border: 1px solid #000;
                                border-radius: 8px;
                                padding: 10px 30px 10px 30px;
                            }

                            li {
                                flex-grow: 1;
                                list-style: disc;
                                text-transform: capitalize;

                                input {
                                    border: none;
                                    outline: none;
                                    font-size: 16px;
                                    width: 100%;
                                }

                                textarea {
                                    height: auto;
                                    border: none;
                                    outline: none;
                                }
                            }
                        }

                    }

                    .qpeEditableText {
                        width: 100%;
                        height: 100%;
                        font-size: 16px;
                        border: none;
                        resize: none;
                        outline: none;
                        background-color: #fff;
                        text-align: justify;
                    }


                    .qpeSessionData {
                        display: flex;
                        border: 1px solid var(--border-color);
                        align-items: center;
                        flex: 1 1;
                        background: #40413C;
                        flex-direction: column;
                        padding: 16px 0;
                        justify-content: space-between;

                        .qpeTimerContainer {
                            color: #fff;
                        }
                    }

                    .qpeProfile {
                        padding: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 100px;
                        border-radius: 50%;
                        height: 100px;
                        background: var(--primary-color);

                        & p {
                            color: var(--white);
                            font-size: 35px;
                        }
                    }

                    .qpeEditButton {
                        position: absolute;
                        top: 5px;
                        right: 5px;
                        padding: 5px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: none;
                        border-radius: 50%;
                        border: none;

                        &:hover {
                            background: #ccc;
                        }
                    }

                    .qpeTextContainer {
                        text-align: justify;
                        overflow-y: auto;
                        display: flex;
                        flex-direction: column;
                        padding-right: 10px;

                        &.qpeEditableText {
                            width: 100%;
                            outline: none;
                            flex: 1 1;
                        }


                    }
                }
            }
        }

        .qpeButtonContainer {
            max-height: 56px;
            align-items: center;
            display: flex;
            max-width: 140px;
            justify-content: center;
            padding: 10px;
            border-radius: 95px;
            background: #1c1a13;
            gap: 20px;
            position: absolute;
            bottom: 40px;
            right: 0;
            left: 0;
            margin: auto;

            .qpeScribe-button {
                display: flex;
                align-items: center;
                border-radius: 50%;
                justify-content: center;
                border: none;
                cursor: pointer;

                &.micIcon {
                    background: #373630;
                }
            }
        }
    }
}