.qpeIndex-container {
    padding: 10px;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: center;
    color: var(--text-color, #333);
    text-align: center;
    padding-top: var(--top-padding);

    .qpeIndex-heading {
        font-size: 2rem;
        margin-bottom: 2rem;
        color: var(--primary-color, #26425A);
    }

    .qpeButton-container {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 0.5rem;
    }

  

    .qpeIndex-button {
        padding: 0.75rem 1.5rem;
        font-size: 1rem;
        color: white;
        background-color: var(--primary-color);
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
            background-color: #ccc;
            color: #000;
        }

        &:focus {
            outline: none;
            box-shadow: 0 0 0 3px rgba(102, 168, 207, 0.5);
        }
    }
}