.qpeHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    padding: 12px 24px;
    position: sticky;
    color: var(--dark-text-color);
    top: 0;
    background: #FAF9F8;
    z-index: 99999;
    border-bottom: 1px solid var(--border-color);
    right: 0;
    left: 0;

    .qpeSubHeader {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        .qpeLeftContainer {
            display: flex;
            align-items: center;
            gap: 10px;

            .qpeHeaderH1 {
                margin: 0;
                font-size: 1.5rem;
                font-weight: 500;
                letter-spacing: 0.5px;
            }
        }

        .qpeRightContainer {
            margin-left: auto;

            .qpeCloseButton {
                display: flex;
                margin-left: auto;
                align-items: center;
                background: none;
                border: none;
                cursor: pointer;
            }
        }
    }
}