* {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

:root {
  --top-padding: 72px;
  --primary-color: #3D77C5;
  --secondary-color: #C38EB4;
  --text-color: #232F47;
  --dark-text-color: #000;
  --background-color: #E1CBD7;
  --button-hover-color: #5B97B5;
  --white: #fff;
  --border-color: #D8D8D8;
}

.mainContainer {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  flex-direction: column;
}

.custom-scrollbar {
  overflow-y: auto;
}

.custom-scrollbar::-webkit-scrollbar {
  display: none;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: lightgreen;
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #26425A;
}

.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) #f1f1f1;
}

.walkinButton:hover {
  cursor: pointer;
}

.initButton {
  position: fixed;
  right: 85px;
  display: flex;
  align-items: center;
  bottom: 30px;
  background-color: var(--primary-color);
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1000;
}

.react-ext-container {
  animation: slideUp 0.6s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 12px;
  bottom: 0;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  height: 612px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  position: fixed;
  right: 20px;
  max-width: 732px;
  width: 100%;
  z-index: 1001;
  transform: scale(0.95);
  opacity: 0;
}

@keyframes slideUp {
  0% {
    bottom: -50%;
    transform: scale(0.95);
    opacity: 0;
  }

  60% {
    bottom: 3%;
    transform: scale(1.02);
    opacity: 1;
  }

  100% {
    bottom: 2%;
    transform: scale(1);
    opacity: 1;
  }
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}