.qpeLoader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100dvh - 70px);
}

.qpeSpinner {
    width: 50px;
    height: 50px;
    border: 5px solid rgba(0, 0, 0, 0.1); // light border color
    border-top-color: #3498db; // primary color for spinner
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}